<template>
  <div>

    <img alt="Vue logo" src="../assets/logo.png" style="display:none">


    <b-alert v-if="alert" show variant="warning">본 사이트는 연습용 사이트이기 때문에 실제 코로나 진단 방식과 결과가 100% 정확하지 않다는 것을 미리 알려드립니다. <b-button variant="outline-primary" v-on:click="closeAlert">X</b-button></b-alert>

    

    <div>
      <b-card
        overlay
        img-src="https://picsum.photos/900/250/?image=3"
        img-alt="Card Image"
        text-variant="white"
        title="체크체크"
        sub-title="빠른 코로나 자가 진단 테스트"
      >

      <div id="checker">
        
        <div class="wrapper">

          <div v-if="caseOne" id="caseOne">
              <div class="Symptom">

                  <h1 class="title">최근 해외를 방문하신적이 있습니까?</h1>

                  <div class="pannel">
                  
                      <b-form-group label="" v-slot="{ ariaDescribedby }">
                        <div class="radio">
                          <b-form-radio v-model="overseas" :aria-describedby="ariaDescribedby" name="some-radios" value="있음"> 예</b-form-radio>
                        </div>

                        <div class="radio">
                        <b-form-radio v-model="overseas" :aria-describedby="ariaDescribedby" name="some-radios" value="없음"> 아니오</b-form-radio>
                        </div>
                      </b-form-group>
                      
                      <div class="button">
                        <b-button variant="outline-primary" v-on:click="next(overseas, '1')" style="margin: 0;">다음</b-button>
                      </div>


                  </div>

              </div>
          </div>


          <div v-if="caseTwo" id="caseTwo">
              <div class="Symptom">

                  <h1 class="title">발열, 마른기침 및 피로감이 있습니까?</h1>

                  <div class="pannel">
                  
                      <b-form-group label="" v-slot="{ ariaDescribedby }">
                        <div class="radio">
                          <b-form-radio v-model="commonSymptoms" :aria-describedby="ariaDescribedby" name="some-radios" value="있음"> 예</b-form-radio>
                        </div>

                        <div class="radio">
                        <b-form-radio v-model="commonSymptoms" :aria-describedby="ariaDescribedby" name="some-radios" value="없음"> 아니오</b-form-radio>
                        </div>
                      </b-form-group>
                      
                      <div class="button">
                        <b-button variant="outline-secondary" v-on:click="back('2')">뒤로</b-button>
                        <b-button variant="outline-primary" v-on:click="next(commonSymptoms, '2')">다음</b-button>
                      </div>


                  </div>

              </div>
          </div>


          <div v-if="caseThree" id="caseThree">
              <div class="Symptom">

                  <h1 class="title">호흡 곤란 또는 숨가쁨 및 가슴 통증, 압박감이 느껴집니까?</h1>

                  <div class="pannel">
                  
                      <b-form-group label="" v-slot="{ ariaDescribedby }">
                        <div class="radio">
                          <b-form-radio v-model="seriousSymptoms" :aria-describedby="ariaDescribedby" name="some-radios" value="있음"> 예</b-form-radio>
                        </div>

                        <div class="radio">
                        <b-form-radio v-model="seriousSymptoms" :aria-describedby="ariaDescribedby" name="some-radios" value="없음"> 아니오</b-form-radio>
                        </div>
                      </b-form-group>
                      
                      <div id="result" class="button">
                        <b-button variant="outline-secondary" v-on:click="back('3')">뒤로</b-button>
                        <b-button variant="outline-primary" v-on:click="processResult(seriousSymptoms,'3')">최종결과</b-button>
                      </div>


                  </div>

              </div>

          </div>



          <div v-if="result" id="result">
              <div class="Symptom">

                  <h1 class="title">최종 진단표</h1>

                  <div class="pannel">
                  
                      <div class="result">

                          <div>
                            <b-table stacked :items="items"></b-table>
                          </div>

                          <p>해외 방문 여부</p>  <p>{{this.overseas}}</p>
                          <hr/>
                          <p>공통적인 증상 여부</p>  <p>{{this.commonSymptoms}}</p>
                          <hr/>
                          <p>심각한 증상 여부</p>  <p>{{this.seriousSymptoms}}</p>
                          <hr/>


                          <p>현재 상황은 <b v-bind:style="{color:statusResult}">{{this.status}}</b>입니다.</p>
                          <b-button variant="outline-secondary" onClick="javascript:location.href='/'">재검사</b-button>

                      </div>

                      <div id="poster">

                        <a href="/img/covid_poster.93cc2424.jpg" target="_blank">
                          <img alt="Vue logo" src="../assets/img/covid_poster.jpg">
                        </a>

                      </div>

                  </div>

              </div>

          </div>




        </div>


      </div>

        <b-card-text>
        코로나19 감염 시의 증상은 사람마다 다릅니다.<br>대부분의 감염자가 경증에서 중증 수준의 증상을 보이며 입원 없이 회복합니다.
        </b-card-text>
      </b-card>



    </div>





  </div>
</template>


<script>


export default {

  data() {
      return {
        alert: true,
        caseOne: true,
        caseTwo: false,
        result: false,
        overseas: null, // caseOne
        commonSymptoms: null, // caseTwo
        seriousSymptoms: null, // caseThree
        status: null,
        score: 0,
        statusResult: 'black'
  
      }
  },

  methods:{
        closeAlert: function(){
          this.alert=false;
        },
        processResult: function(arg1, arg2){

          if(this.overseas == '있음')
            this.score += 1;
          if(this.commonSymptoms == '있음')
            this.score += 2;
          if(this.seriousSymptoms == '있음')
            this.score += 4;

          if(this.score == 0)
          {
            this.status = '안심'
            this.statusResult = 'green';
          }
          else if(this.score <= 3)
          {
            this.status = '의심'
            this.statusResult = 'rgb(255, 189, 0)';
          }
          else if(this.score >= 4)
          {
            this.status = '심각';
            this.statusResult = 'red';

          }

          this.next(arg1, arg2);
        },
        back: function(arg){
            switch(arg){
              case '2':
                this.caseTwo=false;
                this.caseOne=true;
              break;

              case '3':
                this.caseThree=false;
                this.caseTwo=true;
              break;
            }
        },
        next: function(arg1, arg2){

          if( arg1 == null )
            alert('선택 사항을 골라주세요.')
          else
          {
            switch(arg2)
            {
              case '1':
                this.caseOne=false;
                this.caseTwo=true;
              break;

              case '2':
                this.caseTwo=false;
                this.caseThree=true;
              break;

              case '3':
                this.caseThree=false;

                this.result=true;
              break;

            }
          }
        
        }
      
  },
  name: 'Tester',
  props: {
    msg: String
  }
}
</script>

